import React, { Component } from 'react';
import fetch from 'isomorphic-fetch';
import SearchString from '../view/searchString';
import Table from '../view/table';
import { getColumnsForTable, getDataForTable } from './prepareTableData';
import login from './firebaseAutification';

class MainContainer extends Component {
  classes = {
    container: {
      width: '99%',
      margin: '10px auto',
      textAlign: 'center',
    },
  }

  constructor() {
    super();
    this.state = {};
    this.state.mentor = '';
    this.state.data = null;
    this.state.columns = null;
    this.state.sourceJSON = { mentors: {}, tasks: {} };
  }

  componentDidMount() {
    fetch('source.json')
      .then(response => response.json())
      .then((data) => {
        const prevMentor = localStorage.getItem('mentor');
        const newState = {};
        newState.sourceJSON = data;
        if (prevMentor) {
          newState.mentor = prevMentor;
          if (Object.keys(data.mentors).includes(prevMentor)) {
            newState.data = getDataForTable(prevMentor, data);
            newState.columns = getColumnsForTable(prevMentor, data);
          } else {
            newState.data = null;
            newState.columns = null;
          }
        } else {
          newState.mentor = '';
          newState.data = null;
          newState.columns = null;
        }
        this.setState(newState);
      });
  }

    handleChange = (event) => {
      const newState = {};
      const { sourceJSON } = this.state;
      newState.mentor = event.value;
      newState.data = getDataForTable(event.value, sourceJSON);
      newState.columns = getColumnsForTable(event.value, sourceJSON);
      newState.sourceJSON = sourceJSON;
      this.setState(newState);
      localStorage.setItem('mentor', event.value);
    }

    loginHandler = () => {
      login().then((response) => {
        const user = response.additionalUserInfo.username;
        const { sourceJSON } = this.state;
        const newState = {};
        newState.mentor = user;
        newState.sourceJSON = sourceJSON;
        if (Object.keys(sourceJSON.mentors).includes(user)) {
          newState.data = getDataForTable(user, sourceJSON);
          newState.columns = getColumnsForTable(user, sourceJSON);
        } else {
          this.state.data = null;
          this.state.columns = null;
        }
        this.setState(newState);
        localStorage.setItem('mentor', user);
      });
    };

    render() {
      const {
        data,
        columns,
        mentor,
        sourceJSON,
      } = this.state;
      return (
        <div>
          <div style={this.classes.container}>
            <SearchString handleChange={this.handleChange} value={mentor} sourceJSON={sourceJSON} />
            <button type="button" onClick={this.loginHandler}>Login with Github</button>
          </div>
          {(data && columns)
            ? <Table data={data} columns={columns} />
            : <div style={this.classes.container}>No data found</div>}
        </div>
      );
    }
}

export default MainContainer;
