import React from 'react';

export function getColumnsForTable(mentorGithub, sourceJSON) {
  const columns = [{
    Header: 'Task',
    accessor: 'task',
    Cell: row => (
      <a href={row.row.task.link}>{row.row.task.task}</a>),
  }];

  const mentor = sourceJSON.mentors[mentorGithub];
  Object.keys(mentor.students).forEach((student) => {
    columns.push({
      Header: () => (<a href={`${mentor.students[student].github}`}>{student}</a>),
      accessor: `${student}`,
      Cell: row => (
        <div
          style={{
            width: '100%',
            height: '100%',
            backgroundColor: `${row.value}`,
          }}
        />
      ),
    });
  });
  return columns;
}

export function getDataForTable(mentorGithub, sourceJSON) {
  const { tasks } = sourceJSON;
  const mentor = sourceJSON.mentors[mentorGithub];

  const data = [];

  Object.keys(tasks).forEach((task) => {
    const row = { };
    row.task = {};
    row.task.task = task;
    row.task.link = tasks[task].link;

    Object.keys(mentor.students).forEach((student) => {
      switch (tasks[task].status) {
        case 'Checking':
          row[student] = 'red';
          break;
        case 'Checked':
          row[student] = 'darkred';
          break;
        case 'In Progress':
          row[student] = 'yellow';
          break;
        case 'ToDo':
          row[student] = 'gray';
          break;
        default:
      }

      mentor.students[student].tasks.some((studentTask) => {
        if (studentTask === tasks[task].validatedName) {
          row[student] = 'green';
          return true;
        }
        return false;
      });
    });
    data.push(row);
  });
  return data;
}
