import React from 'react';
import PropTypes from 'prop-types';
import Select from 'react-select';

function SearchString(props) {
  const { sourceJSON, handleChange, value } = props;

  const options = Object.keys(sourceJSON.mentors).map(mentor => ({ value: `${mentor}`, label: `${mentor}` }));

  const classes = {
    container: {
      display: 'inline-block',
      width: '70%',
      margin: '10px auto',
    },
    label: {
      display: 'inline-block',
      fontWeight: 'bold',
    },
    input: {
      display: 'inline-block',
      width: '50%',
      margin: '0 5px',
    },
  };

  return (
    <React.Fragment>
      <div style={classes.label}>Mentor:</div>
      <div style={classes.input}>
        <Select
          value={{ value: `${value}`, label: `${value}` }}
          style={classes.input}
          options={options}
          onChange={handleChange}
        />
      </div>
    </React.Fragment>
  );
}

SearchString.propTypes = {
  handleChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  sourceJSON: PropTypes.shape({}).isRequired,
};

export default SearchString;
